a{
    -webkit-text-decoration: navajowhite;
            text-decoration: navajowhite
}
.footer{
    position: fixed;
    bottom: 0;
    background-color:#108ee9;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 50px;
    border-top: 1px solid #ddd;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -webkit-transition: bottom .2s;
    -o-transition: bottom .2s;
    transition: bottom .2s;
    z-index: 100;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    -ms-flex-align: center;
    align-items: center;
    color: #ccc;
}
.footer li{
    text-align: center;
    -ms-flex-positive: 1;
        flex-grow: 1;
}
.footer .am-flexbox-item{
   text-align: center
}
.footer a{
    color: #ccc;
    text-align: center;
}
.footer .footer-icon{
    width: 22px;
    height: 22px;
    vertical-align: middle;
}
.icon-Text{
    font-size: 12px;
    margin-top: 3px;
}
.Footer-activeClassName {
   color: #F00 !important;
   display: block
}


